body {
  background-color: #f8f8e8;
  margin: 0;
  padding: 0;
}

*, :before, :after {
  box-sizing: border-box;
}

.container {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 400px;
  margin: 100px auto;
  display: flex;
}

.controls {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin-bottom: 20px;
  display: flex;
}

.score {
  background-color: #d3cdc5;
  border-radius: 5px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100px;
  height: 100px;
  font-family: sans-serif;
  font-size: 24px;
  font-weight: bold;
  display: flex;
}

.field {
  background-color: #bfad96;
  border-radius: 5px;
  width: 400px;
  height: 400px;
  padding: 10px;
  position: relative;
}

.tile {
  background-color: #eddfb7;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  font-family: sans-serif;
  font-size: 36px;
  font-weight: bold;
  transition: top .15s linear, left .15s linear;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
}

.bgTile {
  background-color: #c9c1b7;
  border-radius: 5px;
  position: absolute;
}

.reset-button {
  cursor: pointer;
  background-color: #f08985;
  border: 0;
  border-radius: 5px;
  outline: none;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100px;
  height: 100px;
  font-family: sans-serif;
  font-size: 24px;
  font-weight: bold;
  transition: background-color .15s linear;
  display: flex;
}

.reset-button:hover {
  background-color: #f1827e;
}

.reset-button:active {
  background-color: #f37570;
}
